import { fetchExtensionConfig } from './extension-config';
import { SettingsProvider, supportedLanguages as defaultSupportedLanguages } from '@project/common/settings';
import { ExtensionSettingsStorage } from './extension-settings-storage';
const stringsKeyForLang = (lang) => `locStrings-${lang}`;
const versionKeyForLang = (lang) => `locVersion-${lang}`;
const settings = new SettingsProvider(new ExtensionSettingsStorage());
export const fetchLocalization = async (lang) => {
    return ((await cachedStringsForLang(lang)) ??
        (await bundledStringsForLang(lang)) ??
        (await bundledStringsForLang('en')));
};
export const fetchSupportedLanguages = async () => {
    const config = await fetchExtensionConfig();
    if (config === undefined) {
        return defaultSupportedLanguages;
    }
    return config.languages.map((c) => c.code);
};
export const primeLocalization = async (lang) => {
    try {
        let config = await fetchExtensionConfig();
        if (config === undefined) {
            return;
        }
        const langConfig = config.languages.find((c) => c.code === lang) ??
            (await fetchExtensionConfig(true))?.languages.find((c) => c.code === lang);
        if (langConfig === undefined) {
            return;
        }
        const versionKey = versionKeyForLang(lang);
        const version = (await chrome.storage.local.get(versionKey))[versionKey];
        if (version === undefined || version < langConfig.version) {
            await fetchAndCache(langConfig);
        }
    }
    catch (e) {
        console.error(e);
    }
};
const fetchAndCache = async ({ code, url, version }) => {
    const asbplayerUrl = await settings.getSingle('streamingAppUrl');
    const effectiveUrl = url.startsWith('http://') || url.startsWith('https://') ? url : `${asbplayerUrl}/${url}`;
    try {
        const strings = await (await fetch(effectiveUrl)).json();
        if (typeof strings === 'object') {
            const versionKey = versionKeyForLang(code);
            const stringsKey = stringsKeyForLang(code);
            await chrome.storage.local.set({ [stringsKey]: strings, [versionKey]: version });
        }
    }
    catch (e) {
        console.error(e);
    }
};
const bundledStringsForLang = async (lang) => {
    for (const defaultLang of defaultSupportedLanguages) {
        if (lang === defaultLang) {
            return {
                lang,
                strings: await (await fetch(chrome.runtime.getURL(`asbplayer-locales/${lang}.json`))).json(),
            };
        }
    }
    return undefined;
};
const cachedStringsForLang = async (lang) => {
    const stringsKey = stringsKeyForLang(lang);
    const strings = (await chrome.storage.local.get(stringsKey))[stringsKey];
    if (strings === undefined) {
        return undefined;
    }
    return { lang, strings };
};
