import { initialGlobalState } from '@project/common/global-state';
export class ExtensionGlobalStateProvider {
    constructor(storage) {
        this._storage = storage ?? chrome.storage.local;
    }
    async getAll() {
        return (await this._storage.get(initialGlobalState));
    }
    async get(keys) {
        const partialInitialGlobalState = {};
        for (const key of keys) {
            partialInitialGlobalState[key] = initialGlobalState[key];
        }
        return (await this._storage.get(partialInitialGlobalState));
    }
    async set(state) {
        await this._storage.set(state);
    }
}
